@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');



#publications{
  width: 100%;
}

.publications-container{
  width:100%;
  max-width: 920px;
  align-items: stretch;
  font: inherit;
  margin: 10px auto;
  padding: 0;
  position: relative;
}
.post-container{
  aspect-ratio: 1 / 1;
  width:100%;
  overflow: hidden;
}
.media-container{
  padding: 1px;
}
.publication-content-container{
  float: left;
  width: 83%;
  padding-left: 2px;
}

.infinite-scroll-component{
  display: inline-grid;
  max-width:800px;
  grid-template-columns: auto auto auto;
  padding: 10px;
}
.publication-image{
  object-fit: cover;
  width: 100%;
}

/* fallback */
@font-face {
  font-family: 'Material Symbols Rounded';
  font-style: normal;
  font-weight: 100 700;
  src: url(https://fonts.gstatic.com/s/materialsymbolsrounded/v75/sykg-zNym6YjUruM-QrEh7-nyTnjDwKNJ_190Fjzag.woff2) format('woff2');
}

.material-symbols-rounded {
  color: black;
/*  height: 30px;
  width: 30px;*/
  font-family: 'Material Symbols Rounded';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}

.material-symbols-rounded  {
  float: left;
  margin-top: 9px;
  margin-left: 20px;
  font-variation-settings:
  'FILL' 0,
  'wght' 250,
  'GRAD' -100,
  'opsz' 40
}
.material-symbols-rounded:hover  {
  transform: scale(1.1, 1.1);
  cursor: pointer;
    font-variation-settings:
  'FILL' 0,
  'wght' 250,
  'GRAD' 100,
  'opsz' 40
}



/*loader*/

:root {
  --main-bg-color: black;
}

.lds-ellipsis {
  width: 70px;
  height: 70px;
  margin: 0 auto;
  transform:scale(0.6, 0.6);
  display: inline-block;
  border-radius: 1%;
  align-items: stretch;
  box-sizing:border-box;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  font: inherit;
  font-size: 100%;
  margin: 10px auto;
  padding: 0;
}
.lds-ellipsis div {
  background-color: var(--main-bg-color);
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #fff;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  background-color: var(--main-bg-color);
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  background-color: var(--main-bg-color);
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  background-color: var(--main-bg-color);
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  background-color: var(--main-bg-color);
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
